@font-face {
    font-family: 'Geometria';
    src: local('Geometria Regular'), local('Geometria-Regular'),
    url('../fonts/Geometria-Regular/Geometria-Regular.woff2') format('woff2'),
    url('../fonts/Geometria-Regular/Geometria-Regular.woff') format('woff'),
    url('../fonts/Geometria-Regular/Geometria-Regular.ttf') format('truetype');
    font-weight: 400;
    font-display: swap;
    font-style: normal;
}
@font-face {
    font-family: 'Geometria';
    src: local('Geometria Medium'), local('Geometria-Medium'),
    url('../fonts/Geometria-Medium/Geometria-Medium.woff2') format('woff2'),
    url('../fonts/Geometria-Medium/Geometria-Medium.woff') format('woff'),
    url('../fonts/Geometria-Medium/Geometria-Medium.ttf') format('truetype');
    font-weight: 500;
    font-display: swap;
    font-style: normal;
}
@font-face {
    font-family: 'Geometria';
    src: local('Geometria Bold'), local('Geometria-Bold'),
    url('../fonts/Geometria-Bold/Geometria-Bold.woff2') format('woff2'),
    url('../fonts/Geometria-Bold/Geometria-Bold.woff') format('woff'),
    url('../fonts/Geometria-Bold/Geometria-Bold.ttf') format('truetype');
    font-weight: 700;
    font-display: swap;
    font-style: normal;
}

